<template>
  <dl class="s-item-info">
    <dt>
      <img :src="meta.image" alt="meta.title">
    </dt>
    <dd>
      <h3>
        <router-link :to="{name:'productDeatil'}">{{ meta.title }}</router-link>
      </h3>
      <p class="m-product-comment">
        <el-rate
          v-model="meta.score"
          disabled
          score-template="{value}">
        </el-rate>
        <span v-if="meta.score > 4">很好</span>
        <span v-else-if="meta.score > 3">好</span>
        <span v-else-if="meta.score > 2">不错</span>
        <span v-else-if="meta.score > 1">一般</span>
        <span v-else>很差</span>
        <span>{{ meta.score }}分</span>
        <span class="s-item-comment">{{ meta.commentNum }}人评论</span>
      </p>
      <p>
        <span class="s-item-tab">
          <span 
            v-for="(item, index) in meta.tab"
            :key="item + index"
          >{{ item + ( index < meta.tab.length-1 ? '|' : '' ) }}</span>
        </span>
        <span class="s-item-address">
          {{ meta.address }}
        </span>
        <span>
          <a href="#">
            <i class="el-icon-location-information"></i>
            查看地图
          </a>
        </span>
      </p>
      <p>
        <span class="s-item-price">
          人均￥{{ meta.avgPrice }}
        </span>
      </p>
      <div class="line"></div>
      <ul class="deal-item">
        <li 
          class="items"
          v-for="(dealItem, index) in meta.dealItems.slice(0, 2)"
          :key="Math.random() + index"
        >
          <p class="deal-title">
            {{ dealItem.title }}
          </p>
          <p class="deal-price-info">
            <span class="deal-price">
              ￥{{ dealItem.price }}
            </span>
            <span class="deal-old-price">
              门市价￥{{ dealItem.counterPrice }}
            </span>
            <span class="deal-sales">
              已售{{ dealItem.saleNum }}
            </span>
          </p>
        </li>
      </ul>
    </dd>
  </dl>
</template>

<script>
export default {
  props:[
    'meta'
  ],
  data() {
    return {
    
    }
  },
}
</script>

<style lang="less">
.el-icon-location-information{
  color: #FF9900;
}
</style>

<style lang="less" scoped>
.s-item-info{
  border-top: 1px dashed #e5e5e5;
  padding: 20px 0 16px;
  display: flex;
  min-height: 125px;
  &:last-of-type{
    padding-bottom: 0;
  }
  dt{
    width: 220px;
    height: 125px;
    img{
      width: 100%;
      height: 100%;
      border-radius: 5px;
    }
  }
  dd{
    font-size: 12px;
    color: #666;
    padding-left: 20px;
    text-align: left;
    width: 688px;
    .deal-item{
      width: 100%;
      margin-top: 10px;
      .items{
        padding: 6px 10px 8px;
        margin-left: -10px;
        transition: background-color .2s;
        cursor: pointer;
        .deal-title{
          font-size: 14px;
          color: #666;
          overflow: hidden;
          white-space: nowrap;
          text-overflow: ellipsis;
        }
        .deal-price-info{
          span{
            margin-right: 10px;
          }
          .deal-price{
            font-size: 15px;
            color: #ff6600;
          }
          .deal-old-price{
            font-size: 12px;
            color: #999;
          }
          .deal-sales{
            font-size: 12px;
            color: #999;
          }
        }
        &:hover{
          background-color: #F8F8F8;
          border-radius: 4px;
          .deal-title{
            color: #f60;
          }
        }
      }
    }
    h3{
      font-size: 16px;
      font-weight: 500;
      color: #333;
    }
    &>p{
      margin-top: 6px;
    }
    p{
      &.m-product-comment{
        display: flex;
        span{
          margin-right: 10px;
          &.s-item-comment{
            color: #FF9900;
          }
        }
      }
      .s-item-address{
        margin-right: 10px;
      }
    }
    
  }
  .line{
    border-bottom: 1px solid #e5e5e5;
    width: 100%;
    padding-top: 10px;
  }
}
</style>