<template>
  <div class="default-nav" :class="nav.class">
    <dl @mouseover="over">
      <dt class="default-title default-item mf-shang-hei-regular">
        {{ nav.title }}
      </dt>
      <dd
        v-for="item in nav.list"
        :key="item.text"
        class="default-item"
        :data-type="item.dp"
        :class="[item.class, { active: kind == item.dp }]"
      >
        {{ item.text }}
      </dd>

      <dd class="default-item default-more">
        <a href="#">
          全部
          <i class="iconfont icon-arrow"></i>
        </a>
      </dd>
    </dl>
  </div>
</template>

<script>
export default {
  data() {
    return {
      kind: 'fs'
    }
  },
  watch:{
    kind(){
      this.$emit("update", this.kind)
    },
  },
  props: ['nav'],
  methods: {
    over(e){
      let dom = e.target;
      let m_tagName = e.target.tagName.toLowerCase();
      if(m_tagName != 'dd') {
        return;
      }
      let dataType = dom.getAttribute('data-type');
      if(dataType) {
        this.kind = dataType;
      }
    }
  },
};
</script>

<style lang="less" scoped>
</style>