<template>
    <div class="filter-component">
      <div class="label">
        <h4>{{ title }}</h4>
        <a class="tag tag-active" href="javascript:void(0);">
          <span>全部</span>
        </a>
      </div>
      <div class="tags">
        <div
          v-for="(item, index) in renderList"
          :key="index"
        >
          <a 
            class="tag" 
            href="javascript:void(0)"
            @mouseenter="showMore(item, index)"
            @mouseleave="hidMore"
          >
            <span>{{ item.title }}</span>
            <i class="el-icon-caret-bottom"></i>
          </a>
        </div>
      </div>
    </div>
</template>

<script>
export default {
  props:[
    'list',
    'title',
  ],
  methods: {
    showMore(data, index) {
      this.$emit('show-pop', index, data);
    },
    hidMore(){
      this.$emit('hidden-pop')
    }
  },
  computed: {
    renderList(){
      return this.list;
    }
  }
}
</script>
<style lang="less">
.el-icon-caret-bottom:before{
  font-size: 12px;
  margin-left: 4px;
}
</style>
<style lang="less" scoped>
.filter-component{
  padding: 15px 20px 0;
  div.label{
    display: flex;
    width: 160px;
    position: absolute;
    &>h4{
      text-align: left;
      width: 80px;
      height: 22px;
      line-height: 22px;
      cursor: default;
    }
    &>.tag{
      height: 22px;
      line-height: 24px;
      display: inline-block;
      padding: 0 10px;
    }
  }
  .tags{
    margin-left: 160px;
    margin-top: -3px;
    line-height: 28px;
  }
  .tags{
    display: flex;
    flex-wrap: wrap;
    &>div{
      min-width: 120px;
      text-align: left;
    }
    .tag{
      display: inline-block;
      padding: 0 10px;
    }
  }
  &:nth-last-of-type(n+2)>div.tags{
    border-bottom: 1px solid #e5e5e5;
    padding-bottom: 10px;
  }
}

</style>