<template>
  <div
    :class="['city-title', className]"
  >
    <h3 class="l-title">
      {{ cityInfo.title }}
    </h3>
    <p 
      class="r-info"
      v-if="cityInfo.info"
    >
      <a 
        href="#"
        v-for="item in cityInfo.info"
        :key="item.id"
      >
      {{ item.name }}
      </a>
    </p>
    <p
      class="r-info"
      v-else
    >
      <a 
        :href="'#city-' + item"
        v-for="item in cityInfo.alphabet"
        :key="item"
      > 
        {{ item }}
      </a>
    </p>
  </div>
</template>

<script>
export default {
  props:[
    'className',
    'cityInfo'
  ]
}
</script>

<style lang="less" scoped>
.city-title{
  display: flex;
  .l-title{
    font-weight: 500;
  }
  .r-info{
    max-width: 900px;
    a{
      margin: 0 20px;
      color: #666;
      display: inline-block;
    }
  }
}
.alphabet{
  display: flex;
  .r-info{
    max-width: 1000px;
    a{
      font-size: 15px;
      margin: 0 9px;
      margin-top: -2px;
      width: 25px;
      height: 25px;
      line-height: 25px;
      color: #666;
      border-radius: 50%;
      box-sizing: border-box;
      text-align: center;
      display: inline-block;
      
      &:hover{
        background-color: #f8f8f8;
        color: #222;
        font-weight: 500;
      }
    }
  }
  
}
</style>