<template>
  <div class="my-header-search">
    <div class="my-search">
      <input 
        type="text" 
        v-model="inpVal" 
        placeholder="搜索商家或地点" 
        @focus="changeState(true)"
        @blur="changeState(false)"
        @input="input"
      />
      <button type="submit" class="sea-btn">
        <span class="iconfont icon-sousuo"></span>
      </button>
    </div>
    <div class="hot-place">
      <ul class="hot-list">
        <li
          v-for="(item, index) in hotPlaceList"
          :key="item + '~' + index"
        >
        <router-link :to="{name:'goods',params:{name:item} }" >
          {{ item }}
        </router-link>
        </li>
        
      </ul>
    </div>
      
    <div class="search-result-box" v-if="isFocus">
      <div 
        class="search-noinput"
        v-if="noInpVal"
      >
        <div class="search-history" 
        >
          <h3>最近热门搜索</h3>
          <ul class="hot-place-list">
            <li
              v-for="(item, index) in hotPlaceList"
              :key="item + '_' + index"
            >
              <router-link :to="{name:'goods', params:{name: item}}">
                {{ item }}
              </router-link>
            </li>
          </ul>
        </div>
      </div>

      <div 
        class="search-hasinput"
        v-if="hasInpVal"
      >
        <ul>

          <li
            v-for="(item, index) in searchList"
            :key="item + '-' + index"
          >
            <router-link :to="{name:'goods', params: { name: item } }">
              {{ item }}
            </router-link>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
import api from '@/api/api'
export default {
  data() {
    return {
      inpVal: '',
      isFocus: false,
      hotPlaceList: [],
      searchList: [],
    }
  },
  computed: {
    noInpVal(){
      return !this.inpVal && this.isFocus;
    },
    hasInpVal(){
      return this.inpVal && this.isFocus;
    }
  },
  watch: {
    '$route.params.name': function () {
      this.inpVal = this.$route.params.name
    }
  },
  methods: {
    changeState(state) {
      if(state){
        this.isFocus = state
      }else{
        let self = this;
        setTimeout(() => {
          self.isFocus = state;
        }, 200);
      }
    },
    input(){
      let val = this.inpVal;
      api.getSearchList().then(res => {
        this.searchList = res.list.filter(item => {
          return item.indexOf(val) > -1;
        })

      })
    }
  },
  created() {
    api.searchHotWords().then(res => {
      this.hotPlaceList = res;
    })
  },
};
</script>

<style lang="less" scoped>
.my-header-search {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  padding-top: 28px;
  z-index: 999;
  &::before{
    content: '';
    display: table;
    height: 0;
    visibility: hidden;
    clear: both;
  }
  .my-search {
    width: 550px;
    height: 40px;
    background: #fff;
    button{
      cursor: pointer;
    }
    input {
      border: 1px solid #e5e5e5;
      border-right: none;
      border-radius: 4px 0 0 4px;
      line-height: 100%;
      display: inline-block !important;
      padding: 15px;
      height: 100%;
      width: 85.45%;
      box-sizing: border-box;
      outline: none;
    }
    ::-webkit-input-placeholder {
      color: #999;
    }
    .sea-btn {
      vertical-align: top;
      width: 14.55%;
      height: 100%;
      line-height: 100%;
      background-color: #ffc300;
      border: none;
      outline: none;
      border-top-right-radius: 4px;
      border-bottom-right-radius: 4px;
    }
  }
  .search-result-box {
    width: 85.45%;
    border: 1px solid #e5e5e5;
    border-top: none;
    background-color: #fff;
    border-radius: 0 0 4px 4px;
    box-shadow: 0 3px 5px 0 rgba(0, 0, 0, .1);
    color: #999;
    font-size: 12px;
    text-align: left;
    position: absolute;
    top: 68px;
    z-index: 999;
    .search-noinput{
      padding: 10px;
      .search-history{
        h3{
          font-weight: 600;
          padding-bottom: 5px;
        }
        .hot-place-list{
          display: flex;
          padding: 5px 3px 10px;
          flex-wrap: wrap;
          li{
            width: 33%;
            margin-top: 5px;
            &:hover{
              a{
                color: #FE8C00;
              }
            }
          }
        }
      }
    }
    .search-hasinput{
      ul{
        li{
          a{
            display: block;
            padding: 3px 10px;
            color: #333;
            height: 25px;
            line-height: 25px;
            &:hover{
              background-color: #f8f8f8;
              color: #FE8C00;
            }
          }
        }
      }
    }
  }
  .hot-place{
    padding: 8px 0 0 12px;
    height: 25px;
    line-height: 17px;
    font-size: 12px;
    overflow: hidden;
    width: 550px;
    box-sizing: border-box;
    text-align: left;
    .hot-list{
      display: flex;
      li{
        margin-right: 5px;
        a:hover{
          color: #FE8C00;
        }
      }
    }
  }
}
</style>