<template>
  <div class="left-content">
    <div class="filter-box">
      <div 
        class="popover"
        :class="[
          'pos-' + showType,
          'pos-' + showIndex
        ]"
        v-if="showMoreTags"
        @mouseenter="showMore"
        @mouseleave="hiddenMore"
      >
        <div class="arrow"></div>
        <div class="content">
          <dl>
            <dt>美食</dt>
            <dd>全部</dd>
            <dd
              v-for="(tag) in moreTags"
              :key="tag.id"
            >{{ tag.name }}</dd>
          </dl>
        </div>
      </div>
      <div 
        class="filter-selected"
        v-if="showFilterTags"
      >
          <h4>已选条件</h4>
          <div class="filter-tags">
            <div>
              <a class="filter-tag" href="javascript:void(0)">
                <span>更多地方菜</span>
                <i class="el-icon-close"></i>
              </a>
            </div>
            <a class="small-font" href="javascript:void(0)">
              <span>· 清除全部</span>
            </a>
          </div>
      </div>
      <div class="filter-section-wrapper">
        <nav-menu 
          :list="classifyList" 
          :title="classifyTitle" 
          @show-pop="showMoreList(arguments)"
          @hidden-pop="hiddenMoreList"
        />
        <nav-menu 
          :list="areaList" 
          :title="areaTitle" 
          @show-pop="showMoreList(arguments)"
          @hidden-pop="hiddenMoreList"
        />
      </div>
    </div>
    <div class="common-list">
      <list />
    </div>
  </div>
</template>

<script>
import api from '@/api/api'
import navMenu from './navMenu'
import list from './list'
export default {
  components: {
    navMenu,
    list
  },
  data() {
    return {
      showFilterTags: false,
      classifyList: [],
      classifyTitle: "分类",
      areaList:  [{
                    "title": "推荐商圈",
                    "subList": [{
                        
                        "name": "望京",
                        "id": 120000
                    }, {
                        
                        "name": "昌平",
                        "id": 12222
                      }] 
                    }, 
                    {
                      "title": "南岗区",
                      "subList": [{
                          "name": "中央大街",
                          
                          "id":123
                      }, {
                          "name" : "西客站",
                          
                          "id": 11
                      }, {
                          
                          "name": "花园街",
                          "id":222
                      }, {
                          
                          "name": "通乡街/果园街",
                          "id": 21
                      }, {
                          
                          "name": "爱建社区",
                          "id": 1
                      }, {
                          
                          "name": "学府路",
                          "id":34
                      }]
                    }],
      areaTitle: "区域",

      moreTags:[],
      showMoreTags: false,
      showIndex: 0,

      timer: null,
      timer1: null
    }
  },
  
  methods: {
    showMoreList(args) {
      clearTimeout(this.timer);
      this.timer = setTimeout(_ => {
        this.moreTags = args[1].subList;
        this.showIndex = args[0];
        this.showType = args[1].type;
        this.showMoreTags = true;
      }, 500)
    },
    showMore(){
      clearTimeout(this.timer1)
      this.showMoreTags = true;
    },
    hiddenMore(){
      this.showMoreTags = false;      
    },
    hiddenMoreList() {
      clearTimeout(this.timer)
      this.timer1 = setTimeout(_ => {
        this.showMoreTags = false;
      }, 100)
    },
    
  },
  created() {
    api.getClassify().then(res => {
      this.classifyList = res;
    })
  }
}
</script>

<style lang="less">
.tag-active{
  color: #222;
  background: linear-gradient(to bottom right,#FFD000,#FFBD00);
  border-radius: 100px;
}
.el-icon-close:before{
  color: #FFC700;
  margin-left: 2px;
  font-weight: 600;
}
.left-content{
  &>div{
    margin-top: 10px;
  }
  .filter-box{
    position: relative;
    background-color: #fff;
    border: 1px solid #e5e5e5;
    border-radius: 5px;
    color: #333;
    font-size: 14px;
    font-weight: 500;
    .popover{
      position: absolute;
      left: 180px;
      //l:312px t:40px
      //192px 90
      //307 90

      top: 40px;
      z-index: 1000;
      user-select: text;
      white-space: normal;
      font-size: 12px;
      line-height: 1.5;
      text-align: left;
      box-shadow: 0 3px 5px 0 rgba(0, 0, 0, .1);
      border: 1px solid #e5e5e5;
      border-radius: 4px;
      background: #fff;
      max-width: 510px;
      &.pos-0{
        left: 192px;
        top: 90px;
      }
      &.pos-1{
        left: 307px;
        top: 90px;
      }
      &.pos-food.pos-0{
        left: 180px;
        top: 40px;
      }
      &.pos-hotal.pos-1{
        left: 312px;
        top: 40px;
      }
      .arrow::before{
        content: '';
        width: 0;
        height: 0;
        border: 5px solid transparent;
        border-bottom-color: #e5e5e5;
        position: absolute;
        left: 19px;
        top: -11px;
      }
      .arrow::after{
        content: '';
        width: 0;
        height: 0;
        border: 5px solid transparent;
        border-bottom-color: #fff;
        position: absolute;
        left: 19px;
        top: -9px;
      }
      .content{
        background-color: #fff;
        background-clip: padding-box;
        border-radius: 4px;
        dl{
          padding: 19px 15px;
          padding-bottom: 9px;
          dt{
            color: #ccc;
            font-size: 16px;
            margin-bottom: 11px;
            cursor: default;
            margin-left: -5px;
          }
          dd{
            display: inline-block;
            min-width: 120px;
            margin-bottom: 5px;
            margin-left: -5px;
            padding: 0 10px;
            cursor: pointer;
          }
        }
      }
    }
    .filter-selected{
      display: flex;
      padding: 11px 20px;
      border-bottom: 1px solid #e5e5e5;
      min-height: 21px;
      line-height: 22px;
      &>h4{
        position: absolute;
        line-height: 24px;
      }
      &>div{
        margin-left: 80px;
      }
      .filter-tags{
        display: flex;
        font-size: 14px;
        &>div{
          margin-right: 10px;
        }
        .filter-tag{
          padding: 0 4px 0 5px;
          border: 1px solid #ffc700;
          border-radius: 10px;
          color: #222;
          background: rgba(255,150,0,.04);
          font-size: 12px;
          display: inline-block;
          height: 18px;
          line-height: 18px;
        }
        .small-font{
          font-size: 12px;
        }
      }
    }
    .filter-section-wrapper{
      padding-bottom: 15px;
    }
  }
  .common-list{
    background: #fff;
    border: 1px solid #e5e5e5;
    border-radius: 4px;
    font-size: 14px;
    line-height: 20px;
  }
}
</style>