<template>
  <div 
    class="categorys-area"
    @mouseenter="showAll(true)"
    @mouseleave="showAll(false)"
  >
    <div
      class="categorys-all"
    >
      全部分类
      <i class="el-icon-arrow-down"></i>
    </div>
    <ul
      class="categorys-list"
      v-show="isShowAll"
    >
      <li
        v-for="nav in allNav"
        :key="nav"
      >
        <a href="#">{{ nav }}</a>
      </li>
    </ul>
  </div>
</template>

<script>
import api from '@/api/api'
export default {
  data() {
    return {
      allNav: [],
      isShowAll: false
    }
  },
  methods: {
    showAll(flag){
      this.isShowAll = flag;
    }
  },
  created() {
    api.getLeftNav().then(res => {
      res.forEach( data => {
        this.allNav.push(data.name);
      });
    })
  },
}
</script>

<style lang="less">
.categorys-area{
  .el-icon-arrow-down{
    position: absolute;
    top: 8px;
    right: 3px;
    width: 6px;
    height: 6px;
    overflow: hidden;
    transform: rotate(0);
    &::before{
      position: relative;
      top: -3px;
      left: -3px;
      display: block;
      transition: all .2s;
    }
  }
  &:hover{
    .el-icon-arrow-down{
      &::before{
        left: 3px;
        top: -2px;
        transform: rotate(-180deg);
      }
    }
  }
}

</style>

<style lang="less" scoped>
.categorys-area{
    height: 22px;
    line-height: 17px;
    display: inline-block;
    margin-left: 14px;
    font-size: 12px;
    color: #999;
    position: relative;
    bottom: 15px;
    border: 1px solid #e5e5e5;
    border-radius: 4px;
    background-color: #fff;
    &:hover{
      position: relative;
      border-radius: 0;
      border-top-left-radius: 2px;
      border-top-right-radius: 2px;
      border-bottom: 0;
      .categorys-all{
      box-shadow: 0 3px 5px 0 rgba(0, 0, 0, .1);

      }
    }
    .categorys-all{
      padding: 3px 13px 3px 4px;
      cursor: pointer;
    }
    .categorys-list{
      position: absolute;
      left: -1px;
      top: 22px;
      background-color: #fff;
      border: 1px solid #e5e5e5;
      border-bottom-left-radius: 2px;
      border-bottom-right-radius: 2px;
      box-shadow: 0 3px 5px 0 rgba(0, 0, 0, .1);
      padding: 15px 40px 12px 10px;
      z-index: 1000;
      &::after{
        content: '';
        position: absolute;
        left: 0;
        top: -2px;
        width: 65px;
        height: 3px;
        background-color: #fff;
      }
      li{
        white-space: nowrap;
        line-height: 2.4;
        &:hover{
          a{
            color: #FE8C00;
          }
        }
        a{
          color: #666;
          padding: 0 6px;
          display: inline-block;
        }
      }
    }
}
</style>