</template>

<script>
import navArea from './navArea/index';
import searchArea from './searchArea/index'
export default {
  components:{
    navArea,
    searchArea
  },
  computed: {
    hasPad(){
      return !this.$route.meta.flag ? true : false
    },
    hasCategorysAll(){
      return this.$route.meta.hasSelect;
    }
  },
  created() {

  },
}
</script>

