<template>
  <div class="left-menu">
    <dl 
      @mouseleave="menuLeave"
    >
      <dt>全部分类</dt>
      <dd
        v-for="(menu, index) in menuList"
        :key="Math.random() + index"
        @mouseenter="menuEnter(menu)"
      >
        <i class="iconfont m-icon" :class="'icon-' + menu.type"></i>
        <span>
          <a href="#">{{ menu.name }}</a>
        </span>
        <i class="iconfont icon-arrow"></i>
      </dd>
    </dl>
    <div 
      class="menu-detail-wrapper" 
      v-if="curDetail"
      @mouseenter="detailEnter"
      @mouseleave="detailLeave"
    >
      <div class="menu-detail-area">
        <div 
          class="menu-detail" 
          >

          <template v-for="(item, index) in curDetail.items">
            <div class="menu-detail-title" :key="index">
              <h2>
                <a href="">{{ item.title }}</a>
              </h2>
              <a href="">
                更多
                <i class="iconfont m-icon icon-arrow"></i>
              </a>
            </div>
            <ul class="detail-list" :key="index + Math.random()">
              <li
                v-for="(item, index) in item.items"
                :key="Math.random() + index"
              >
                <a href="">{{ item }}</a>
              </li>
            </ul>
          </template>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import api from "@/api/api";
export default {
  data() {
    return {
      curDetail: null,
      menuList: [],
    };
  },
  methods: {
    menuEnter(item) {
      const _this = this;
      clearTimeout(this.timerEnter);
      this.timerEnter = setTimeout(_ => {
        _this.curDetail = item;
      }, 200)
    },
    menuLeave() {
      const self = this;
      this.timer = setTimeout(_ => {
        self.curDetail = null;
      }, 200)
    },
    detailEnter(){
      clearTimeout(this.timer);
    },
    detailLeave(){
      this.curDetail = null;  
    }
  },
  mounted() {
    api.getLeftNav().then((res) => {
      this.menuList = res;
    });
  },
};
</script>

<style lang="less" scoped>
.left-menu {
  width: 230px;
  height: 475px;
  border: 1px solid #e5e5e5;
  background-color: #fff;
  margin-top: -50px;
  position: relative;
  dl {
    text-align: left;
    padding-bottom: 8px;
    dt {
      height: 60px;
      padding-top: 15px;
      box-sizing: border-box;
      color: #222;
      font-size: 16px;
      margin-left: 15px;
      font-weight: 700;
    }
    dd {
      position: relative;
      padding: 2px 12px;
      height: 26px;
      line-height: 26px;
      vertical-align: middle;
      box-sizing: border-box;
      &:hover {
        background-color: rgba(255, 150, 0, 0.08);
        a {
          font-weight: 700;
          color: #222;
        }
        .icon-arrow::after {
          color: #222;
        }
      }
      .icon-arrow::after {
        position: absolute;
        right: 10px;
        top: 0;
      }
      span {
        margin-left: 11px;
        display: inline-block;
        vertical-align: top;
        line-height: 20px;
        a {
          font-size: 13px;
          color: #646464;
        }
      }
    }
  }
  .menu-detail-wrapper {
    position: absolute;
    left: 233px;
    top: 60px;
    z-index: 100;
    width: 400px;
    height: 416px;
    background-color: #fff;
    color: #666;
    .menu-detail-area {
      //display: none;
      .menu-detail {
        padding: 0 30px;
        .menu-detail-title {
          margin-top: 24px;
          height: 22px;
          line-height: 22px;
          padding-bottom: 10px;
          display: flex;
          justify-content: space-between;
          border-bottom: 1px solid #e5e5e5;
          h2 {
            a {
              font-size: 16px;
              font-weight: 500;
              color: #222;
            }
          }
          a {
            font-size: 12px;
            font-weight: 400;
            color: #999;
            margin-right: 6px;
            position: relative;
            vertical-align: middle;

            .icon-arrow {
              position: absolute;
              right: -9px;
              top: 5px;
              font-size: 12px;
              width: 9px;
              height: 9px;
              overflow: hidden;
            }
            .icon-arrow::after {
              position: absolute;
              left: -1px;
              top: -5px;
            }
          }
        }
        .detail-list {
          display: flex;
          flex-wrap: wrap;
          li {
            a {
              color: #999;
              font-size: 12px;
              margin-top: 10px;
              margin-right: 16px;
              display: inline-block;
              line-height: 16px;
              &:hover {
                color: #fe8c00;
              }
            }
          }
        }
      }
    }
  }
}
</style>