<template>
  <div 
    :class="['m-casader', className, disabled ? 'disabled' : '']"
    @click="showContent(true, $event)"
    v-document-click="documentClick"
  >
    <span>{{ value }}</span>
    <i class="el-icon-caret-bottom"></i>
    <div 
      :class="['mt-content', className, {active: isShowContent}]"
      
    >
      <h2 class="title">{{ title }}</h2>
      <div 
        :class="['mt-wrapper', className]"
        
      >
        <div 
          class="col"
          v-for="(listData, index) in renderList"
          :key="Math.random() + index"
        >
          <span 
            v-for="(item,index) in listData"
            :key="Math.random() + index"
            @click="changeValue(item, $event)"
            :class="{active: item.name === value}"
          >
            {{ item.name }}
          </span>
            

        </div>
      </div>
    </div>
  </div>
</template>

<script>

export default {
  props: [
        'list',
        'value',
        'title',
        'isShowContent',
        'className',
        'disabled'
      ],
  data() {
    return {
      
    }
  },
  computed: {
    renderList() {
      if(!this.list.length) return;
      const len = Math.ceil( this.list.length / 12 );
      let resultList = [];
      for(let i = 0;i < len; i ++){
        resultList.push( this.list.slice(i * 12, i * 12 + 12) )
      }
      return resultList;
    },
  },
  methods: {
    showContent(flag, e) {
      e.stopPropagation();
      if(!this.disabled){
        this.$emit('change_active', flag);
      }
    },
    documentClick() {
      this.$emit('change_active', false);
    },
    changeValue(item, e) {
      this.showContent(false, e);
      this.$emit('change', item);
    }
  },
};
</script>

<style lang="less" scoped>
.m-casader {
  &.disabled{
    cursor: not-allowed;
  }
  &.m-province{
    margin-left: 10px;
    margin-right: 10px;
  }
  &.m-city{
    margin-left: 10px;
    margin-right: 20px;
  }
  padding: 10px 0 10px 10px;
  cursor: pointer;
  border: 1px solid #e5e5e5;
  font-size: 14px;
  width: 150px;
  height: 40px;
  line-height: 20px;
  box-sizing: border-box;
  text-align: left;
  border-radius: 4px;
  color: #666;
  position: relative;
  i.el-icon-caret-bottom {
    position: absolute;
    top: 0;
    bottom: 0;
    right: 4px;
    color: #666;
    height: 10px;
    margin: auto 0;
    transform: scale(0.8);
  }
  .mt-content {
    display: none;
    position: absolute;
    left: 0;
    top: 45px;
    height: 375px;
    background-color: #fff;
    border: 1px solid #e5e5e5;
    border-radius: 4px;
    box-shadow: 0 3px 5px 0 rgba(0, 0, 0, 0.1);
    padding: 20px 0 20px 15px;
    box-sizing: border-box;
    z-index: 999;
    &.active{
      display: block;
    }
    &.m-province{
      width: 264px;
    }
    &.m-city{
      min-width: 150px;
    }
    &::before {
      content: "";
      display: inline-block;
      position: absolute;
      left: 31px;
      top: -13px;
      border: 6px solid transparent;
      border-bottom-color: #e5e5e5;
    }
    &::after {
      content: "";
      display: inline-block;
      position: absolute;
      left: 31px;
      top: -11px;
      border: 6px solid transparent;
      border-bottom-color: #fff;
    }
    .title{
      font-size: 16px;
      color: #ccc;
      margin-bottom: 11px;
    }
    .mt-wrapper{
      &::after{
        content: '';
        display: block;
        height: 0;
        clear: both;
      }
      &.m-city{
        display: flex;
        span:hover{
          color: #222;
          font-weight: 500;
        }
      }
      &.m-province{
        display: flex;
        span:hover{
          border-radius: 10px;
          background: #f4f4f4;
        }
      }
      .col{
        float: left;
        span{
          font-size: 12px;
          color: #666;
          margin: 6px 38px 6px 0;
          padding: 0 8px;
          min-width: 40px;
          height: 20px;
          box-sizing: border-box;
          display: table;
          white-space: nowrap;
          &.active{
            background: linear-gradient(to bottom right,#FFD000,#FFBD00);
            border-radius: 10px;
            color: #222;
          }
        }
      }
    }
  }
}

</style>