<template>
  <div class="alphabet-city-area">
    <template
      v-for="(citys, alpha) in cityList"
    >
      <div 
      :key="alpha"
      v-if="citys.length"
      class="city-area"
      :id="'city-' + alpha"
    >
        <span class="city-label">
          {{ alpha }}
        </span>
        <span class="citys">
          <a 
            href="#" 
            v-for="(city, index) in citys"
            :key="Math.random() + index"
            :class="['city', {rank: city.rank === 'S' || city.rank === 'A'}]"
          >
            {{ city.name }}
          </a>
        </span>
      </div>
    </template>
    
  </div>
</template>

<script>
import api from '@/api/api'
export default {
  props:[
    'allCity',
    'alphabet'
  ],
  data() {
    return {
      // cityList: {},
      cityGroup: this.allCity,
    }
  },
  computed: {
    cityList() {
      let obj = {};
      let m_cityList = {};
      this.cityGroup.forEach(city => {
        if(!obj[city.firstChar.toUpperCase()]){
          obj[city.firstChar.toUpperCase()] = [];
        }
        obj[city.firstChar.toUpperCase()].push(city);
      })
      this.alphabet.forEach(item => {
        if(obj[item]){
          m_cityList[item] = this.mySort(obj[item], 'rank');
        }
      })
      return m_cityList;
    }
  },
  methods: {
    mySort(arr, prop) {
      arr.sort(function (a, b) {
        let val1 = a[prop],
            val2 = b[prop];
        if (val1 > val2) {
          return 1;
        } else if (val1 < val2) {
          return -1;
        } else {
          return 0;
        }
      })
      let Scity = null;
      arr.forEach((item, index) => {
        if( item[prop] === 'S' ) {
          Scity = arr.splice(index, 1);
          arr.unshift(...Scity);
        }
      })
      return arr;
    }
  },
  created() {
  },
}
</script>

<style lang="less" scoped>
.alphabet-city-area{
  text-align: left;
  .city-area{
    padding: 13px 30px 13px 10px;
    border-radius: 10px;
    transition: background-color .5s;
    &:hover{
      background-color: #f8f8f8;
    }
    .city-label{
      display: inline-block;
      width: 40px;
      height: 40px;
      line-height: 40px;
      background-color: #ffd000;
      color: #222;
      text-align: center;
      border-radius: 50%;
      vertical-align: top;
    }
    .citys{
      max-width: 1065px;
      display: inline-block;
      .city{
        display: inline-block;
        margin: 10px 20px;
        color: #666;
        font-size: 14px;
        height: 19px;
        line-height: 19px;
        &.rank{
          color: #FF6600;
        }
      }
    }
  }
}
</style>