<template>
  <div class="m-product-list">
    <ul
      class="sort-option"
    >
      <li
        v-for="(item) in nav"
        :key="item.key"
        :class="{'options-active': item.active}"
        @click="changeActive(item)"
      >{{ item.name }}</li>
    </ul>
    <el-row class="m-pro-info">
      <main-info 
        v-for="(item, index) in productList"
        :key="index"
        :meta="item"
      />
    </el-row>
  </div>
</template>

<script>
import api from '@/api/api'
import mainInfo from './mainInfo'
export default {
  components: {
    mainInfo
  },
  data() {
    return {
      nav: [
        {
          key: 's-default',
          name: '智能排序',
          active: true
        },
        {
          key: 's-price',
          name: '价格最低',
          active: false
        },
        {
          key: 's-score',
          name: '人气最高',
          active: false
        },
        {
          key: 's-comment',
          name: '评价最高',
          active: false
        }
      ],
      productList: []
    }
  },
  methods: {
    changeActive(item) {
      this.nav.forEach(obj => {
        obj.active = false;
      })
      item.active = true;
    },
  },
  created() {
    api.getProductList().then(res => {
      this.productList = res;
    })
  }
}
</script>

<style lang="less" scoped>
.sort-option{
  padding: 15px 20px 2px;
  display: flex;
  margin-left: -20px;
  li{
    box-sizing: border-box;
    min-width: 96px;
    padding: 0 10px;
    color: #666;
    font-weight: normal;
    cursor: pointer;
    &.options-active{
      color: #222;
      font-weight: 500;
    }
  }
}
.m-pro-info{
  padding: 11px 20px;
}
</style>