<template>
  <div class="city-container">
    <el-row class="op-area">
      <el-col :span="10">
        <div class="choose-by-province">
          <h3>按省份选择：</h3>
          <div class="choose-wrap">
            <m-cascader 
              :value="province" 
              title="省份"
              className="m-province"
              :isShowContent="showProvince"
              @change_active="changeShowPro"
              :list="provinceList"
              @change="changeProvinceValue"
            />
            <m-cascader 
              :value="city" 
              title="城市"
              :isShowContent="showCity"
              @change_active="changeShowCity"
              @change="changeCityValue"
              :disabled="cityDisable"
              className="m-city"
              :list="cityList"
            />
          </div>
        </div>
      </el-col>
      <el-col :span="7">
        <div class="search">
          <h3>直接搜索：</h3>
          <el-select
            v-model="searchWord"
            filterable
            remote
            no-match-text
            reserve-keyword
            placeholder="请输入关键词"
            :remote-method="remoteMethod"
            :loading="loading">
            <el-option
              v-for="item in options"
              :key="item.name"
              :value="item.name">
              {{ item.name }}
            </el-option>
          </el-select>
        </div>
      </el-col>
    </el-row>
    <el-row class="city-list">
      <l-title-r-info 
        :className="hotCity"
        :cityInfo="hotInfo"
      />
    </el-row>
    <el-row class="city-list">
      <l-title-r-info 
        :className="recentCity"
        :cityInfo="recentInfo"
      />
    </el-row>
    <el-row class="city-list alphabet-city">
      <l-title-r-info 
        :className="alphabet"
        :cityInfo="alphabetInfo"
      />
    </el-row>
    <el-row>
      <city-alphabet  
        :allCity="allCity" 
        :alphabet="alphabetInfo.alphabet"
      />
    </el-row>
  </div>
</template>

<script>
import { mapState } from 'vuex';
import api from "@/api/api";
import mCascader from '@/components/changeCity/cascader.vue'
import lTitleRInfo from '@/components/changeCity/Lt-Ri.vue'
import cityAlphabet from '@/components/changeCity/cityAlphabet.vue'
export default {
  components:{
    mCascader,
    lTitleRInfo,
    cityAlphabet
  },
  computed:{
    
  },
  data() {
    return {
      provinceList: [],
      province: '省份',
      cityList: [],
      city: '城市',
      showProvince: false,
      showCity: false,
      cityDisable: true,

      hotCity: "hot-city",
      hotInfo: {
        title: "热门城市：",
        info: [],
      },
      recentCity: "recent-city",
      recentInfo: {
        title: "最近搜索：",
        info: [],
      },
      alphabet: "alphabet",
      alphabetInfo:{
        title: "按拼音首字母选择：",
        alphabet: "ABCDEFGHIJKLMNPQRSTWXYZ".split(''),
      },

      cityByAlphabet: [],

      loading: false,
      filterable: true,
      remote: true,
      options: [],
      list: [],

      allCity: [],
      searchWord: '',
      timeout: null,
    };
  },
  methods: {
    remoteMethod(word){
      let allCity = this.allCity;
      this.options = word ? allCity.filter(this.createFilter(word)) : allCity;
    },
    createFilter(item) {
      return (city) => {
          return (city.pinyin.toLowerCase().indexOf(item.toLowerCase()) === 0)
                || (city.name.indexOf(item) === 0);
        };
    },
    changeShowPro(flag) {
      this.showProvince = flag;
      if(flag) {
        this.showCity = !flag;
      }
    },
    changeShowCity(flag) {
      this.showCity = flag;
      if(flag) {
        this.showProvince = !flag;
      }
    },
    changeProvinceValue(val) {
      this.province = val.name;
      this.cityDisable = false;
      this.cityList = val.cityInfoList;
    },
    changeCityValue(val) {
      this.city = val.name;
      this.$store.dispatch('setPosition', val);
      this.$router.push({name:'Index'})
    },
    getAllCityList() {
      this.provinceList.map( item => item.cityInfoList).forEach( ele => {
        this.allCity.push(...ele);
      });
    }
  },
  created() {
    api.getProvinceList().then((res) => {
      this.provinceList = res.map(item => {
          item.name = item.provinceName;
          return item;
      }) 

      this.getAllCityList();

    });
    // console.log(this.provinceList);
    // this.provinceList.map( item => item.cityInfoList).forEach( ele => {
    //   this.allCity.push(...ele);
    // });

    api.getHotCityList().then(res => {
      this.hotInfo.info = res;
    })
    api.getRecentList().then(res => {
      this.recentInfo.info = res;
    })

    
  },
};
</script>

<style lang="less">
.op-area{
  padding-bottom: 30px;
  border-bottom: 1px solid #e5e5e5;
}
.el-select-dropdown{
  top: 271px !important;
}
.el-select{
  margin-left: 10px;
  input{
    padding-left: 10px;
  }
}
</style>

<style lang="less" scoped>
.city-container {
  width: 1190px;
  margin: 20px auto 0;
  padding: 20px;
  box-sizing: border-box;
  min-height: 1000px;
  border: 1px solid #e5e5e5;
  border-radius: 5px;
  background-color: #fff;
  .op-area{
    .choose-by-province{
      display: flex;
      align-items: center;
      h3{
        font-size: 16px;
        color: #333;
        font-weight: 500;
      }
      .choose-wrap{
        display: flex;
      }
    }
    .search{
      display: flex;
      align-items: center;
      h3{
        font-size: 16px;
        color: #333;
        font-weight: 500;
      }
    }
  }

  .city-list{
    border-bottom: 1px solid #e5e5e5;
    padding: 30px 0;
  }
  .alphabet-city{
    border: none;
  }
}
</style>